$imgp: "../images/";

@-webkit-keyframes totop {
	0% {
		-webkit-transform: translateY(-3px);
		transform: translateY(-3px);
	}
	50% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
	}
	100% {
		-webkit-transform: translateY(-3px);
		transform: translateY(-3px);
	}
}

@keyframes totop {
	0% {
		-webkit-transform: translateY(-3px);
		transform: translateY(-3px);
	}
	50% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
	}
	100% {
		-webkit-transform: translateY(-3px);
		transform: translateY(-3px);
	}
}
@mixin back($i, $r, $v) {
	background-image: url($imgp + $i + ".png");
	background-repeat: no-repeat;
	background-size: $r $v;
}
